@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Pangolin:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-manrope: Manrope;
  --font-pangolin: Pangolin;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-xl: 20px;
  --font-size-21xl: 40px;
  --font-size-45xl: 64px;
  --font-size-13xl: 32px;

  /* Colors */
  --color-gray-100: #2e2c2c;
  --black: #242321;
  --color-gray-200: rgba(255, 255, 255, 0);
  --white: #fff;
  --red: #b80300;

  /* Gaps */
  --gap-base: 16px;
  --gap-xs: 12px;

  /* Paddings */
  --padding-5xs: 8px;
  --padding-45xl: 64px;
}
