.div {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.5;
}
.copyIcon,
.div1 {
  position: absolute;
  left: 1px;
}
.div1 {
  top: 55px;
  line-height: 24px;
}
.copyIcon {
  top: 103px;
  width: 32px;
  height: 32px;
  overflow: hidden;
}
.phone {
  position: absolute;
  top: 48px;
  left: 48px;
  width: 180px;
  height: 135px;
}
.div3,
.mapIcon {
  position: absolute;
  left: 0;
}
.div3 {
  top: 41px;
  line-height: 52px;
}
.mapIcon {
  top: 103px;
  width: 32px;
  height: 32px;
  overflow: hidden;
}
.adress {
  position: absolute;
  top: 48px;
  left: 437px;
  width: 432px;
  height: 135px;
}
.closeIcon {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 24px;
  height: 24px;
  overflow: hidden;
  opacity: 0.5;
}
.phoneParent {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--black);
}
.phoneParent,
.popUp,
.popUpParent {
  width: 917px;
  height: 231px;
}
.popUp {
  position: absolute;
  top: 0;
  left: 0;
}
.popUpParent {
  position: relative;
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--white);
  font-family: var(--font-manrope);
}
